import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import CIcon from '@coreui/icons-react'
import { cilFindInPage, cilMinus } from '@coreui/icons'
import {
  CFormInput,
  CFormTextarea,
  CRow,
  CCol,
  CButton,
  CTable,
  CTableHead,
  CTableRow,
  CTableHeaderCell,
  CTableBody,
  CTableDataCell,
  CModal,
  CModalHeader,
  CModalTitle,
  CModalFooter,
  CModalBody,
  CFormSelect,
} from '@coreui/react'

import { quotationContent } from '../../mocks/quotationData'

const QuotationList = () => {
  const { childPath } = useParams()
  const [open, setOpen] = useState(false)
  const [currentItemIdx, setCurrentItemIdx] = useState(0)
  const [basicInfo, setBasicInfo] = useState({
    company: '',
    taxID: '',
    contactPerson: '',
    email: '',
    phone: '',
    address: '',
  })
  const [itemDataLists, setItemDataLists] = useState([
    {
      productDes: null,
      startDate: null,
      partNum: null,
      measure: null,
      quantity: null,
      unitPrice: null,
      QTY: null,
      country: null,
      totalAmount: null,
      discount: null,
      discountUnitPrice: null,
      discountTotalAmount: null,
    },
  ])

  const openModal = (index) => {
    console.log('index', index)
    setOpen(true)
    setCurrentItemIdx(index)
  }

  const addItem = () => {
    const newDataLists = [
      ...itemDataLists,
      {
        productDes: null,
        partNum: null,
        startDate: null,
        measure: null,
        quantity: null,
        unitPrice: null,
        QTY: null,
        country: null,
        totalAmount: null,
        discount: null,
        discountUnitPrice: null,
        discountTotalAmount: null,
      },
    ]
    setItemDataLists(newDataLists)
  }

  const deleteItem = (index) => {
    if (itemDataLists && itemDataLists.length === 1) return
    let newItemDataLists = JSON.parse(JSON.stringify(itemDataLists))
    newItemDataLists.splice(index, 1)
    setItemDataLists(newItemDataLists)
  }

  const setCurrentItemData = () => {
    let newCurrentItem = itemDataLists[currentItemIdx]
    newCurrentItem.productDes = 'M365 Apps Enterprise Device Sub Per Device'
    newCurrentItem.partNum = '1GJ-00001'
    newCurrentItem.unitPrice = 1000

    let newItemDataLists = itemDataLists
    newItemDataLists[currentItemIdx] = newCurrentItem

    setItemDataLists(newItemDataLists)
    setOpen(false)
  }

  useEffect(() => {
    if (childPath && childPath === 'edit') {
      setBasicInfo(quotationContent.basicInfo)
      setItemDataLists(quotationContent.quotationLists)
    }
  }, [childPath])

  return (
    <>
      <CRow className="form-box">
        <CRow className="mb-3">
          <CCol md={4}>
            <CFormInput label="客戶名稱" placeholder="請輸入客戶名稱" value={basicInfo.company} />
          </CCol>
          <CCol md={4}>
            <CFormInput label="統一編號" placeholder="請輸入統一編號" value={basicInfo.taxID} />
          </CCol>
        </CRow>
        <CRow className="mb-3">
          <CCol md={4}>
            <CFormInput label="聯絡人" placeholder="請輸入姓名" value={basicInfo.contactPerson} />
          </CCol>
          <CCol md={4}>
            <CFormInput
              type="email"
              label="Email"
              placeholder="請輸入Email"
              value={basicInfo.email}
            />
          </CCol>
          <CCol md={4}>
            <CFormInput label="電話" placeholder="請輸入電話" value={basicInfo.phone} />
          </CCol>
        </CRow>
        <CRow>
          <CCol md={12}>
            <CFormInput label="地址" placeholder="請輸入地址" value={basicInfo.address} />
          </CCol>
        </CRow>
      </CRow>

      <CRow className="mb-2">
        <div className="px-0 d-flex justify-content-between align-items-center">
          <h5 className="mb-0">報價項目</h5>
          <CButton color="primary" shape="rounded-pill" onClick={addItem}>
            新增項目
          </CButton>
        </div>
      </CRow>
      <CRow className="form-box px-2 overflow-scroll">
        <CTable hover className="table-bg-none">
          <CTableHead>
            <CTableRow>
              <CTableHeaderCell className="col-2" scope="col">
                product description
              </CTableHeaderCell>
              <CTableHeaderCell scope="col">part number</CTableHeaderCell>
              <CTableHeaderCell scope="col">usage start date</CTableHeaderCell>
              <CTableHeaderCell scope="col">unit of measure</CTableHeaderCell>
              <CTableHeaderCell scope="col">unit of quantity</CTableHeaderCell>
              <CTableHeaderCell scope="col">unit price (TWD)</CTableHeaderCell>
              <CTableHeaderCell scope="col">license QTY</CTableHeaderCell>
              <CTableHeaderCell scope="col">usage country</CTableHeaderCell>
              <CTableHeaderCell scope="col">total amount (TWD)</CTableHeaderCell>
              <CTableHeaderCell scope="col">customer discount %</CTableHeaderCell>
              <CTableHeaderCell scope="col">discount unit price (TWD)</CTableHeaderCell>
              <CTableHeaderCell scope="col">discount total amount (TWD)</CTableHeaderCell>
              <CTableHeaderCell scope="col"></CTableHeaderCell>
            </CTableRow>
          </CTableHead>
          <CTableBody>
            {itemDataLists && itemDataLists.length
              ? itemDataLists.map((item, idx) => {
                  return (
                    <CTableRow key={`${item.productDes}_${idx} `}>
                      <CTableDataCell align="middle" className="d-flex align-items-center">
                        <CFormInput value={item.productDes || ''} />
                        <CIcon
                          icon={cilFindInPage}
                          size="xxl"
                          className="ms-1 action-icon"
                          onClick={() => openModal(idx)}
                        ></CIcon>
                      </CTableDataCell>
                      <CTableDataCell align="middle">
                        <CFormInput value={item.partNum || ''} />
                      </CTableDataCell>
                      <CTableDataCell align="middle">
                        <CFormInput value={item.startDate || ''} />
                      </CTableDataCell>
                      <CTableDataCell align="middle">
                        <CFormInput value={item.measure || ''} />
                      </CTableDataCell>
                      <CTableDataCell align="middle">
                        <CFormInput value={item.quantity || ''} />
                      </CTableDataCell>
                      <CTableDataCell align="middle">
                        <CFormInput value={item.unitPrice || ''} />
                      </CTableDataCell>
                      <CTableDataCell align="middle">
                        <CFormInput value={item.QTY || ''} />
                      </CTableDataCell>
                      <CTableDataCell align="middle">
                        <CFormInput value={item.country || ''} />
                      </CTableDataCell>
                      <CTableDataCell align="middle">
                        <CFormInput value={item.totalAmount || ''} />
                      </CTableDataCell>
                      <CTableDataCell align="middle">
                        <CFormInput value={item.discount || ''} />
                      </CTableDataCell>
                      <CTableDataCell align="middle">
                        {item.discountUnitPrice || '-'}
                      </CTableDataCell>
                      <CTableDataCell align="middle">
                        {item.discountTotalAmount || '-'}
                      </CTableDataCell>
                      <CTableDataCell align="middle">
                        <div className="d-flex align-items-center" onClick={() => deleteItem(idx)}>
                          <div className="action-icon deleted-icon">
                            <CIcon className="text-light" icon={cilMinus} />
                          </div>
                        </div>
                      </CTableDataCell>
                    </CTableRow>
                  )
                })
              : null}
          </CTableBody>
        </CTable>
      </CRow>
      <CRow className="mb-2">
        <div className="px-0 d-flex justify-content-between align-items-center">
          <h5 className="mb-0">備註</h5>
        </div>
      </CRow>
      <CRow className="form-box px-2">
        <CCol md={12}>
          <CFormTextarea rows={3}></CFormTextarea>
        </CCol>
      </CRow>
      <CModal visible={open} onClose={() => setOpen(false)}>
        <CModalHeader>
          <CModalTitle>搜尋產品項目</CModalTitle>
        </CModalHeader>
        <CModalBody>
          <div className="px-2">
            <CFormSelect
              label="產品項目"
              className="mb-2"
              options={[
                '請選擇產品項目',
                { label: 'M365 Apps Enterprise Device Sub Per Device', value: '1GJ-00001' },
                { label: 'Office Professional Plus ALng LSA', value: '269-05623' },
                { label: 'Office Professional Plus ALng SASU Office Standard', value: '269-07493' },
              ]}
            />
            <CFormSelect
              label="Level"
              className="mb-2"
              options={[
                '請選擇',
                { label: 'A', value: 'A' },
                { label: 'B', value: 'B' },
                { label: 'C', value: 'C' },
                { label: 'D', value: 'D' },
              ]}
            />
            <CFormSelect
              label="Purchase Unit"
              className="mb-2"
              options={['請選擇', { label: '1 month(s)', value: '1M' }]}
            />
          </div>
        </CModalBody>
        <CModalFooter>
          <CButton color="secondary" onClick={() => setOpen(false)}>
            Close
          </CButton>
          <CButton color="primary" onClick={setCurrentItemData}>
            選擇
          </CButton>
        </CModalFooter>
      </CModal>
    </>
  )
}

export default QuotationList
